body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Main {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    margin-top: 125px;
    padding: auto;
    font-family: 'Karla', sans-serif;
    color: #353636;
    padding-bottom: 40px;
}
.Personal {
    width: 50%;
}
.About {
    display: flex;
    flex-flow: row;
}
.About img {
    width: auto;
    height: 325px;
    margin: 30px 40px 30px 30px;
    box-shadow: 5px 5px #CCCCCC;
}
.About p {
    line-height: 22px;
    font-size: 15px;
}
.Info h1{
    margin-bottom: 10px; 
}
.Info h4{
    margin: 0;
    padding: 0;
    font-size: 15px;
}
hr {
    border: 1px solid #C7B299;
    border-radius: 5px;
    margin: 20px 0;

}
.links {
    display: flex;
    flex-flow: row;
    justify-content: center;
     margin-top: 10px;
}
.Education p {
    font-size: 14px;
}
.Link i {
    margin-right: 15px;
}
.Link {
    display: flex;
    flex-flow: row;
    margin: 20px 30px;
}
.Link {
    color: #353636;
    text-decoration: none;
}
.Link:hover {
    color: #C7B299;
}

.Link p {
    margin: 8px 0;
}
.Experiences {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin-left: 20px;
}
.Work {
    width: 400px;
    height: 120px;
    margin: 15px 40px;
    background-color: #C7B299;
    border-radius: 10px;
    box-shadow: 4px 4px #CCCCCC;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    padding: 4px;
    border: none;
}
.Work:hover {
    background-color: rgb(236, 203, 165); /* Green */
    box-shadow: 0 0 white;
    -webkit-transform: translateX(3px);
            transform: translateX(3px);
}
.Work:active {
    background-color: rgb(138, 120, 99);
}


.Work:focus {
    outline: 0;
}
.Work h3{
    text-align: center;
    margin-top: 15px;
    color: #353636;
    
}

@media only screen and (max-width: 1300px) {
    .Main {
    display: flex;
    flex-flow: column;
    justify-content: center;
    margin-left: 15%;
    margin-top: 20px;
      }
    .Personal {
        width: 80%;
    }
    .Experiences {
        margin-top: 30px;
        margin-left: 12%;
        width: 80%;
    }
}
@media only screen and (max-width: 940px) {
    .Personal {
        width: 90%;
    }
}
@media only screen and (max-width: 834px) {
    .About img {
        width: 0;
        margin: 0;
    }
    .Experiences {
        margin-left: 8%;
    }
    .Main {
        margin-left: 10%;
    }
}
@media only screen and (max-width: 720px) {
    .links {
        display: flex;
        flex-flow: wrap;
    }
    .Link {
        width: 30%;
    }
    .Experiences {
        margin-left: 0;
    }
}
@media only screen and (max-width: 680px) {
    .Main {
        margin-left: 5%;
    }
    .Experiences {
        margin-left: 0;
        width: 95%;
    }
}
@media only screen and (max-width: 600px) {
    .Work {
        margin: 20px 35px;   
    }
    .Work h3{
        margin-top: 20px;
    }
    .Personal {
        margin-left: 10px;
    }
}
@media only screen and (max-width: 550px) {
    .Work {
     width: 320px;
     height: 100px;
     margin: 15px 20px;
    }
}
@media only screen and (max-width: 400px) {
    .Work {
        margin: 15px 20px 15px 34px;
        width: 270px;
        height: 80px;   
    }
    .Work h3 {
        margin-top: 18px;
    }
    .Link {
        margin-left: 13px;
    }
    .Info h4{
        font-size: 14px;
    }
    .Info h1 {
        font-size: 25px;
    }
}

@media only screen and (max-width: 350px) {
    .Work {
        margin: 15px 5px;
        width: 260px;
        height: 80px;   
    }
    .Info h1{
        font-size: 28px;
    }
    .Link {
        margin-left: 0px;
        width: 33%;
    }
}



.WorkButton {
    margin: 10px;
    width: 300px;
    height: 90px;
    box-shadow: 3px 3px 3px 3px #dbdbdb;
    border-style: none;
    padding: 0;
}
.WorkButton img {
    width: 89px;
    height: 89px;
    float: left;
}
.WorkButton:focus {
    outline: 0;
}
.modal-open {
    overflow:hidden
}    
.modal-open .modal {
    overflow-x:hidden;
    overflow-y:auto;
}
.modal {
    position:fixed;
    top:0;
    left:0;
    z-index:1050;
    display:none;
    width:100%;
    height:100%;
    overflow:hidden;
    outline:0;
}
.hej .modal-dialog {
    display: flex;
    justify-content: center;
    margin:.5rem;
    pointer-events:none;
    
}
@media (prefers-reduced-motion:reduce) {
    .modal.fade .modal-dialog {
        -webkit-transition:none;
        transition:none;
    }
}
.modal.show .modal-dialog {
    -webkit-transform:none;
    transform:none
}
.modal-dialog-scrollable {
    display:flex;
    max-height:calc(100% - 1rem)
}
.modal-dialog-scrollable .modal-content {
    max-height:calc(100vh - 1rem);
    overflow:hidden
}
.modal-dialog-scrollable .modal-footer, .modal-dialog-scrollable .modal-headers {
    flex-shrink:0
}
.modal-dialog-scrollable .modal-body {
    overflow-y:auto;
}
.modal-dialog-centered {
    display:flex;
    align-items:center;
    min-height:calc(100% - 1rem)
}
.modal-dialog-centered::before {
    display:block;
    height:calc(100vh - 1rem);content:""
}
.modal-dialog-centered.modal-dialog-scrollable {
    flex-direction:column;
    justify-content:center;
    height:100%
}
.modal-dialog-centered.modal-dialog-scrollable .modal-content {
    max-height:none;
}
.modal-dialog-centered.modal-dialog-scrollable::before {
    content:none;
}
.hejsan .modal-content {
    position:relative;
    display:flex;
    flex-direction: column;
    pointer-events:auto;
    background-color:#fff;
    background-clip:padding-box;
    border-radius:.3rem;
    outline: 0;
}
.modal-backdrop.fade {
    opacity:0;
}
.modal-backdrop.show .hejsan{
    opacity:0.5;
}
.modal-headers {
    display:flex;
    align-items:flex-start;
    justify-content:space-between;
    padding:1rem 1rem;
    border-bottom:1px solid #dee2e6;
    border-top-left-radius:.3rem;
    border-top-right-radius:.3rem;    
}
.modal-headers .close {
    padding:1rem 1rem;
    margin:-1rem -1rem -1rem auto
}
.modal-title {
    margin-bottom:0;
    line-height:1.5;
}
.modal-body {
    display: flex;
    flex-flow: wrap;
    align-content: flex-start;
    padding:1rem;
    width: 80vw;
    min-height: 75vh;
    justify-content: center;

}

.modal-scrollbar-measure {
    position:absolute;
    top:-9999px;
    width:50px;
    height:50px;
    overflow:scroll
}
@media (min-width:576px) {
    .modal-dialog {
        max-width:500px;
        margin:1.75rem auto
    }
    .modal-dialog-scrollable {
        max-height:calc(100% - 3.5rem)
    }
    .modal-dialog-scrollable 
    .modal-content {
        max-height:calc(100vh - 3.5rem)
    }
    .modal-dialog-centered {
        min-height:calc(100% - 3.5rem)
    }
    .modal-dialog-centered::before {
        height:calc(100vh - 3.5rem)
    }
    .modal-sm{max-width:300px
    }
}
@media (min-width:992px){
    .modal-lg,.modal-xl {
        max-width:800px
    }
}
@media (min-width:1200px) {
    .modal-xl {
        max-width:1140px
    }
}
